.cta-contained {
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  text-align: center;
  font-size: 13px;
  line-height: 17px;
  letter-spacing: 1.5px;
  font-weight: bold;
  text-transform: uppercase;
  height: 48px;
  min-width: 144px;
  padding: 0 18px;
  cursor: pointer;
  outline: 2px solid rgba(0, 0, 0, 0);
  transition: color 0.4s cubic-bezier(0.23, 1, 0.32, 1),
    border 0.4s cubic-bezier(0.23, 1, 0.32, 1),
    transform 0.4s cubic-bezier(0.23, 1, 0.32, 1),
    background-color 0.4s cubic-bezier(0.23, 1, 0.32, 1),
    outline 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}
.cta-contained:active {
  transform: translate(2px, 2px);
}
.cta-contained:focus {
  outline: 2px solid #62aeb0;
}
.cta-contained:disabled {
  pointer-events: none;
  opacity: 0.3;
}
.cta-contained--border {
  border-style: solid;
  border-width: 2px;
}
.cta-contained--border--primary--light {
  border-color: #ffffff;
  color: #ffffff;
}
.cta-contained--border--primary--light:hover:not(.cta-contained--no-hover) {
  background-color: #ffffff;
  color: #1d3060;
}
.cta-contained--border--primary--dark {
  border-color: #1d3060;
  color: #1d3060;
}
.cta-contained--border--primary--dark:hover:not(.cta-contained--no-hover) {
  background-color: #1d3060;
  color: #ffffff;
}
.cta-contained--border--primary--dark:focus {
  outline-color: #0e8386;
}
.cta-contained--border--primary--dark:disabled {
  border-color: #808080;
  color: #808080;
}
.cta-contained--solid {
  border: none;
  position: relative;
}
.cta-contained--solid:hover:not(.cta-contained--no-hover) {
  opacity: 1;
}
.cta-contained--solid:hover:not(.cta-contained--no-hover):after {
  opacity: 1;
}
.cta-contained--solid:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 48px;
  content: "";
  opacity: 0;
  transition: opacity 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}
.cta-contained--solid--primary--dark {
  background-color: #870064;
  color: #ffffff;
  transition: all 1s;
}

.cta-contained--solid--primary--dark:hover {
  background-color: #423d09;
  border: 1px solid goldenrod;
  border-radius: 20px 0px 0px 20px;
}

.cta-contained--solid--primary--dark:after {
  background-color: rgba(255, 255, 255, 0.2);
}
.cta-contained--solid--primary--dark:focus {
  outline-color: #0e8386;
}
.cta-contained--solid--primary--light {
  background-color: #ffffff;
  color: #1d3060;
}
.cta-contained--solid--primary--light:after {
  background-color: rgba(29, 48, 96, 0.3);
}

