.global-cart {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 99999;
  width: 100%;
  height: 100%;
  pointer-events: none;
  visibility: hidden;
  transition: visibility 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}
.global-cart.active {
  pointer-events: all;
  visibility: visible;
}
.global-cart.active .global-cart__overlay {
  cursor: pointer;
  opacity: 1;
  visibility: visible;
}
.global-cart.active .global-cart__drawer {
  transform: translateX(0);
}
.global-cart__overlay {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s cubic-bezier(0.23, 1, 0.32, 1) visibility 0.4s
    cubic-bezier(0.23, 1, 0.32, 1);
}
.global-cart__drawer {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  max-width: 480px;
  background-color: #ffffff;
  transform: translateX(110%);
  transition: transform 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}
.global-cart__drawer__header {
  box-sizing: border-box;
  border-bottom: 1px solid #bebebe;
}
.global-cart__drawer__header .heading {
  flex-grow: 1;
}
.global-cart__drawer__header .close {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
}
.global-cart__drawer__header .close svg g {
  fill: #1d3060;
}
.global-cart__drawer__error-bar {
  height: 0;
  color: #ef5133;
  text-align: center;
  border-bottom: solid 0 rgba(0, 0, 0, 0.1);
  transition: height 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}
.global-cart__drawer__error-bar.active {
  height: 61px;
  border-bottom: solid 1px rgba(0, 0, 0, 0.1);
}
.global-cart__drawer__item-list {
  position: relative;
  flex-grow: 1;
  margin: 0;
  overflow-y: scroll;
  list-style: none;
}
.global-cart__drawer__item-list .item {
  position: relative;
  flex-shrink: 0;
  border-bottom: solid 1px #f8f8f8;
  transition: opacity 0.4s cubic-bezier(0.23, 1, 0.32, 1);
  padding: 22px 18px;
