  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background-color: rgba(29, 48, 96, 0.1);
  content: "";
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.4s cubic-bezier(0.23, 1, 0.32, 1),
    visibility 0.4s cubic-bezier(0.23, 1, 0.32, 1);
}
.global-cart__drawer__item-list .item.loading::after {
  pointer-events: all;
  opacity: 1;
