.banner_wapper {
  width: 100%;
  position: relative;
  z-index: 2;
  padding: 0 10px;
}

.inner_banner {
  width: 100%;

  position: relative;

  padding: 0px 100px 0px 0px;
}

.inner_banner .banner-background {
  width: 100%;

  min-height: 650px;

  background-position: center center;

  background-repeat: no-repeat;

  background-size: cover;
}

.inner_banner .banner-box {
  width: 650px;

  text-align: right;

  position: absolute;

  right: 0px;

  bottom: 100px;

  padding: 70px 100px 90px 100px;
}

.inner_banner .banner-box h2 {
  font-family: "Oswald-Regular";

  font-size: 80px;

  color: #cacaca;

  letter-spacing: 0.2em;

  text-transform: uppercase;
}

.we_turn_main {
  width: 100%;
  display: inline-block;
  vertical-align: top;
  padding: 100px 0 0;
}
.we_turn_main h2 {
  font-family: "Oswald-Regular";
  font-size: 60px;
  color: #857651;
  letter-spacing: 0.2em;
  text-align: center;
  text-transform: uppercase;
  padding: 0 0 74px;
}
.we_turn_main .txt_block p {
  font-family: "ProximaNova-Regular";
  font-size: 13px;
  color: #000;
  line-height: 49px;
  letter-spacing: 0.025em;
  padding-right: 7px;
  margin: 0;
  text-align: justify;
}
.we_turn_main .img-bx {
  width: 100%;
  display: inline-block;
  vertical-align: top;
  position: relative;
}
.we_turn_main .img-bx img {
  width: 100%;
}
.we_turn_main .abt_founder {
  position: absolute;
  bottom: 69px;
  right: 57px;
}
.we_turn_main .abt_founder .sign {
  font-size: 30px;
  color: #fff;
  margin: 0;
  padding: 0;
  position: relative;
  font-family: "HLTLaCarte";
  z-index: 1;
  letter-spacing: 0.025em;
  -webkit-transform: rotate(-5.7deg);
  -moz-transform: rotate(-5.7deg);
  transform: rotate(-5.7deg);
  opacity: 0.7;
}
.we_turn_main .positionb {
  font-size: 30px;
  color: #857651;
  text-transform: uppercase;
  margin: -10px 0 0 0;
  padding: 0;
  font-family: "Oswald-Regular";
  letter-spacing: 0.1em;
  opacity: 0.7;
}

@media (min-width: 768px) {
  .container,
  .container_max {
    width: 750px;
  }
}

