  .container,
  .container_max {
    width: 970px;
  }
}

@media (min-width: 1200px) {
  .container,
  .container_max {
    width: 1170px;
  }
}

@media (min-width: 1440px) {
  .container,
  .container_max {
    width: 1420px;
  }
}

@media (min-width: 1700px) {
  .container_max {
    width: 1684px;
  }
}

.container_max {
  margin: 0 auto;
  padding: 0 15px;
}

.container_max:before,
.container_max:after {
  display: table;
  content: "";
}

.container_max:after {
  clear: both;
}

